import React from 'react'

//components
import Header from './Header' 
import Inventory from './Inventory'
import Order from './Order'
import sampleFishes from '../sample-fishes'
import Fish from './Fish'
import base from '../base'

class App extends React.Component {

    state = {
        fishes: {},
        order : {}
    }

    componentDidMount() {
        const { params } = this.props.match
         this.ref = base.syncState(`${params.storeID}/fishes`, {
            context: this,
            state: "fishes"
         }) ?
         (this.ref = base.syncState(`${params.storeID}/order`, {
            context: this,
            state: "order"
         })) : null
    }
    componentWillUnmount() {
        base.removeBinding(this.ref)
    }

    updateFish = (key, updatedFish) => {
        // take a copy of the state 
        const fishes = {...this.state.fishes}
        // updating the state 
        fishes[key]= updatedFish;
        // set the state to the value
        this.setState({fishes:fishes})
    }

    deleteFish = (key) => {
        // Take a copy of state 
        const fishes = {...this.state.fishes}
        // update the state 
        fishes[key]= null;
        //set the state 
        this.setState({fishes:fishes})
    }

    addFish = (item) => {
         const fishes = {...this.state.fishes}
         // adding a new fish 
        fishes[`item${Date.now()}`] = item
         // setting new state 
         this.setState({
             fishes:fishes
         })
    }

    loadSampleFishes = () =>{
        this.setState({
            fishes: sampleFishes
        })
    }

    addToOrder = (item) =>{
        //1. take a copy of state
        const order = {...this.state.order}
        //2. either add to the order or update the order
        order[item] = order[item] + 1 || 1;
        //3. update the state
        this.setState({
            order:order
        })
    }

    removeFromOrder = key => {
        //1. take a copy of state
        const order = {...this.state.order}
        //2. either add to the order or update the order
        // null is for mirroring the state to firebase
        order[key] = null
        //3. update the state
        this.setState({
            order:order
        })
    }

    render(){
        return(
            
            <div className='catch-of-the-day'>
                <div className='menu'>
                    <Header />
                    <ul className="fishes">
                       {Object.keys(this.state.fishes).map(key =>
                         <Fish key={key} 
                            index={key}
                            details={this.state.fishes[key]} 
                            addToOrder={this.addToOrder} />
                        )}
                    </ul>
                </div>
                <Order 
                    fishes={this.state.fishes} 
                    order={this.state.order}
                    removeFromOrder={this.removeFromOrder}
                />

                <Inventory  
                    addFish={this.addFish} 
                    updateFish={this.updateFish} 
                    loadSampleFishes={this.loadSampleFishes}
                    fishes={this.state.fishes}
                    deleteFish={this.deleteFish}
                    storeid={this.props.match.params.storeID}
                />
            </div>
        )
    }
}

export default App;


