import React from 'react'


const Header = ()=>{
    return(
        <header className='top'>
            <h1> Fish
                <span className='ofThe'>
                    <span className='of'>in</span>
                    <span className='the'>the</span>
                </span>
                  Stores </h1>
            <h3 className='tagline'>
                <span>FRESH SEAFOOD MARKET</span>
            </h3>
           </header>
    )
}

export default Header;


